html {
  --link-height: 60px;
  --dynamic-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  height: var(--link-height);
  line-height: var(--link-height);
  text-transform: uppercase;
  padding: 0;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--dynamic-color);
}

.is-active {
  color: var(--dynamic-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--dynamic-color);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

